.layout-type-grid {
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	-webkit-flex-wrap: wrap;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
}
.advanced-image-gallery-link {
	display: block;
}
.advanced-gallery-img {
	width: 100%;
	object-fit: cover;
	object-position: center;
}
.advanced-image-gallery-item {
	position: relative;
	&:hover {
		.advanced-image-gallery-content {
			opacity: 1;
		}
	}
}
.advanced-image-gallery-content {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	-webkit-flex-flow: column nowrap;
	-ms-flex-flow: column nowrap;
	flex-flow: column nowrap;
	-webkit-box-pack: center;
	-webkit-justify-content: center;
	-ms-flex-pack: center;
	justify-content: center;
	-webkit-box-align: center;
	-webkit-align-items: center;
	-ms-flex-align: center;
	align-items: center;
	text-align: center;
	z-index: 1;
	opacity: 0;
	padding: 10px;
	-webkit-transition: 0.5s;
	-o-transition: 0.5s;
	transition: 0.5s;
	&::before {
		content: '';
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		z-index: -1;
		background-color: rgba(0,0,0,0.6);
	}
	.advanced-gallery-icon,
	.advanced-gallery-title,
	.advanced-gallery-description {
		width: 100%;
	}
}
.advanced-gallery-title {
	h4 {
		color: #ffffff;
	}
}
.advanced-gallery-description {
	p {
		color: #ffffff;
	}
}
.advanced-gallery-icon {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: center;
	.icon-inner {
		width: 40px;
		height: 40px;
		i {
			color: #ffffff;
			font-size: 30px;
		}
	}
}
.advanced-image-gallery-list.layout-type-masonry {
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	-webkit-flex-flow: row nowrap;
	-ms-flex-flow: row nowrap;
	flex-flow: row nowrap;
	-webkit-box-pack: start;
	-webkit-justify-content: flex-start;
	-ms-flex-pack: start;
	justify-content: flex-start;
}
.advanced-image-gallery-list.layout-type-masonry .salvattore-column {
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	-webkit-flex-flow: column nowrap;
	-ms-flex-flow: column nowrap;
	flex-flow: column nowrap;
}
.advanced-image-gallery-list.layout-type-justify {
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	-webkit-flex-flow: row wrap;
	-ms-flex-flow: row wrap;
	flex-flow: row wrap;
	-webkit-box-pack: justify;
	-webkit-justify-content: space-between;
	-ms-flex-pack: justify;
	justify-content: space-between;
	-webkit-box-align: stretch;
	-webkit-align-items: stretch;
	-ms-flex-align: stretch;
	align-items: stretch;
	-webkit-align-content: stretch;
	-ms-flex-line-pack: stretch;
	align-content: stretch;
	.advanced-image-gallery-item {
		-webkit-box-flex: 0;
		-webkit-flex: 0 1 25%;
		-ms-flex: 0 1 25%;
		flex: 0 1 25%;
		height: auto;
		min-height: 100px;
		display: -webkit-box;
		display: -webkit-flex;
		display: -ms-flexbox;
		display: flex;
	}
	.advanced-image-gallery-item-inner {
		display: -webkit-box;
		display: -webkit-flex;
		display: -ms-flexbox;
		display: flex;
	}
	.advanced-gallery-img-wrap {
		height: 100%;
	}
	.advanced-gallery-img {
		 height: 100%;
		-o-object-fit: cover;
		object-fit: cover;
	}
}
.advanced-image-gallery-item-inner {
	position: relative;
	overflow: hidden;
}
.advanced-image-gallery-list {
	.col-desk-1,.col-desk-2,.col-desk-3,.col-desk-4,.col-desk-5,.col-desk-6,.col-tab-1,.col-tab-2,.col-tab-3,.col-tab-4,.col-tab-5,.col-tab-6,.col-mob-1,.col-mob-2,.col-mob-3,.col-mob-4,.col-mob-5,.col-mob-6 {
